import axios from 'axios';
import { Module } from 'vuex';
import { IListItem, IHistoryExam } from '@/types/interface';
import {
  RootState, TestState, ITestItemOrigin, ITestTemplateOrigin,
} from './types';

const pastTest: Module<TestState, RootState> = {
  namespaced: true,
  state: {
    yearList: [],
    typeList: [],
    levelList: [],
    subjectList: [],
    courseList: [],
    historyExamList: [],
    errorQuz: '',
  },
  mutations: {
    SAVE_YEAR_LIST(state, list) {
      state.yearList = list.map((d: ITestItemOrigin) => ({
        id: d.rid,
        title: d.Title,
      }));
    },
    SAVE_TYPE_LIST(state, list) {
      state.typeList = list.map((d: ITestItemOrigin) => ({
        id: d.rid,
        title: d.Title,
      })).sort((a: IListItem, b: IListItem) => (a.id < b.id ? -1 : 1));
    },
    SAVE_LEVEL_LIST(state, list) {
      state.levelList = list.map((d: ITestItemOrigin) => ({
        id: d.rid,
        title: d.Title,
      }));
    },
    SAVE_SUBJECT_LIST(state, list) {
      state.subjectList = list.map((d: ITestItemOrigin) => ({
        id: d.rid,
        title: d.Title,
      })).sort((a: IListItem, b: IListItem) => (a.id < b.id ? -1 : 1));
    },
    SAVE_COURSE_LIST(state, list) {
      state.courseList = list.map((d: ITestItemOrigin) => ({
        id: d.rid,
        title: d.Title,
      })).sort((a: IListItem, b: IListItem) => (a.id < b.id ? -1 : 1));
    },
    SAVE_HISTORY_EXAM_LIST(state, list) {
      state.historyExamList = list.map((d: ITestTemplateOrigin) => ({
        id: d.rid,
        class: d.Class,
        years: d.Years,
        degree: d.Degree,
        name: d.Name,
        subject: d.Subject,
        flagQ: d.flagQ,
        flagA: d.flagA,
      }));
    },
    SAVE_ERROR_QUESTION(state, quz) {
      state.errorQuz = quz;
    },
    SAVE_HISTORY_EXAM_URL(state, data) {
      const itemIdx = state.historyExamList.findIndex((d: IHistoryExam) => d.id === data.id);
      if (itemIdx >= 0) {
        if (data.type === '1') state.historyExamList[itemIdx].quzUrl = data.url;
        else state.historyExamList[itemIdx].solUrl = data.url;
      }
    },
  },
  actions: {
    getYearList({ commit }) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/Testhistory/GetYear_List')
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_YEAR_LIST', data.Data);
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getTypeList({ commit }) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/Testhistory/GetType_List')
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_TYPE_LIST', data.Data);
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getLevelList({ commit }, params) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/Testhistory/GetLevel_List', params)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_LEVEL_LIST', data.Data);
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getSubjectList({ commit }, params) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/Testhistory/GetSubject_List', params)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_SUBJECT_LIST', data.Data);
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getCourseList({ commit }, params) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/Testhistory/GetCourse_List', params)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_COURSE_LIST', data.Data);
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getHistoryExamList({ commit }, params) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/Testhistory/GetHistoryExam_List', params)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') {
              commit('SAVE_HISTORY_EXAM_LIST', data.Data);
              resolve();
            } else reject();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    saveErrorQuestion({ commit }, quz) {
      commit('SAVE_ERROR_QUESTION', quz);
    },
    getUploadFileUrl({ commit }, params) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/Testhistory/GetUploadFileUrl', params)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') {
              commit('SAVE_HISTORY_EXAM_URL', {
                id: params.exam_no,
                type: params.type,
                url: data.Data[0].url,
              });
              resolve();
            } else reject();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    postMemberMisReport({ commit, state, rootState }, params) {
      const temp = {
        ...params,
        // eslint-disable-next-line
        test_id: state.errorQuz ? state.errorQuz.tId : '',
        // eslint-disable-next-line
        quo_type: state.errorQuz ? state.errorQuz.qType : '',
        // eslint-disable-next-line
        account_id: rootState.userId,
      };
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/OnlineTest/Post_Member_Mis_Report', temp)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') resolve();
            else reject();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
  },
  getters: {
    typeList: (state) => state.typeList,
    levelList: (state) => state.levelList,
    subjectList: (state) => state.subjectList,
  },
};

export default pastTest;
