import { Module } from 'vuex';
import { NotificationState, RootState, INotificationOrigin } from '@/store/types';
import axios from 'axios';
import { IActivityItem, IArticle, INotification } from '@/types/interface';

const notification: Module<NotificationState, RootState> = {
  namespaced: true,
  state: {
    notificationList: [],
  },
  mutations: {
    SAVE_NOTIFICATION_LIST(state, list) {
      state.notificationList = list.map((d: INotificationOrigin) => ({
        id: d.NotifID,
        title: d.Subject,
        start: d.NotifDate,
        end: d.NotifDate,
        brief: d.Content.length >= 32 ? `${d.Content.substring(0, 32)} ...` : d.Content,
        content: d.Content,
        notify: d.NotifDate,
        url: d.NotifUrl,
        group: d.NotifGroup,
        unread: d.NotifID !== d.RealID,
        hide: d.ToHide === 1,
        hashtag: d.NotifKeyWordCht.split(',').filter((str: string) => str !== ''),
        keyword: d.NotifKeyWordCht.split(',')[0],
      }));
    },
  },
  actions: {
    getNotificationList({ commit, rootState }) {
      const temp = {
        UserGuid: rootState.userId,
      };
      return new Promise((resolve, reject) => {
        axios
          .post('RaspApp/Notification', temp)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_NOTIFICATION_LIST', data.Data);
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    readNotification({ commit, rootState }, id) {
      const temp = {
        UserGuid: rootState.userId,
        NotifID: id,
      };
      return new Promise((resolve, reject) => {
        axios
          .post('RaspApp/Understand', temp)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') resolve();
            reject();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    hideNotification({ rootState }, id) {
      const temp = {
        UserGuid: rootState.userId,
        NotifID: id,
      };
      return new Promise((resolve, reject) => {
        axios
          .post('RaspApp/NotificaHide', temp)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') resolve();
            reject();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
  },
  getters: {
    activityNotificationById: (state) => (id: number): IActivityItem | undefined => {
      const temp = state.notificationList.find((d: INotification) => d.id === id);
      return temp ? {
        id: temp.id.toString(),
        title: temp.title,
        description: temp.content,
        start: temp.start,
        end: temp.end,
        planStart: temp.start,
        planEnd: temp.start,
        imgSmall: '',
        imgBig: '',
        url: '',
        task: '',
      } : undefined;
    },
    articleNotificationById: (state) => (id: number): IArticle | undefined => {
      const temp = state.notificationList.find((d: INotification) => d.id === id);
      return temp ? {
        id: temp.id,
        title: temp.title,
        url: temp.url,
        imgUrl: '',
        subTitle: temp.notify,
        hashtags: temp.hashtag,
        note: temp.content,
      } : undefined;
    },
  },
};

export default notification;
