import Vue from 'vue';
import Vuex, { StoreOptions } from 'vuex';
import axios from 'axios';
import { IInputInfo } from '@/types/interface';
import { ICommercialOrigin, RootState } from './types';
import pastTest from './pastTest';
import testKind from './testKind';
import ticket from './ticket';
import tags from './tags';
import onlineTest from './onlineTest';
import subscription from './subscription';
import article from './article';
import task from './task';
import examInfo from './examInfo';
import site from './site';
import notification from './notification';

Vue.use(Vuex);

const store: StoreOptions<RootState> = {
  state: {
    registerFlag: false,
    token: '',
    phone: '',
    snapCode: '',
    userId: '',
    name: '',
    email: '',
    memberId: '',
    address: '',
    pId: '',
    ckid: '',
    source: '',
    articleTag: '',
    newsSearch: '',
    approveKey: '',
    linkSource: '',
    messageToken: '',
    device: '',
    commercialList: [],
    topCommercialList: [],
    tempLinkFlag: false,
  },
  getters: {
    isMobileLogin(state) {
      return state.source === 'CF05';
    },
    isProfileReady(state) {
      return state.name !== '' && state.phone !== '' && state.pId !== '';
    },
    topCommercial(state) {
      return state.topCommercialList.length > 3 ? state.topCommercialList.slice(0, 3) : state.topCommercialList;
    },
    isLink(state) {
      return state.linkSource === 'CF04' || state.tempLinkFlag;
    },
  },
  mutations: {
    CHANGE_REGISTER_MODE(state, flag) {
      state.registerFlag = flag;
    },
    REFRESH_TOKEN(state, token) {
      state.token = token;
    },
    SAVE_LOGIN_INFO_SNAPCODE(state, code) {
      state.snapCode = code;
    },
    SAVE_PERSON_ID(state, id) {
      if (id !== null) state.userId = id;
    },
    SAVE_LOGIN_INFO_MOBILE(state, mobile) {
      if (mobile) state.phone = mobile;
    },
    SAVE_LOGIN_INFO_NAME(state, name) {
      if (name !== null) state.name = name;
    },
    SAVE_LOGIN_INFO_EMAIL(state, email) {
      if (email !== null) state.email = email;
    },
    SAVE_LOGIN_INFO_SOURCE(state, source) {
      if (source !== null) state.source = source;
    },
    SAVE_LOGIN_INFO_MEMBER_ID(state, mId) {
      if (mId !== null) state.memberId = mId;
    },
    SAVE_LOGIN_INFO_ID(state, pId) {
      if (pId !== null) state.pId = pId;
    },
    SAVE_LOGIN_INFO_CK_ID(state, ckid) {
      if (ckid !== null) state.ckid = ckid;
    },
    SAVE_LOGIN_INFO_ADDRESS(state, address) {
      if (address !== null) state.address = address;
    },
    SAVE_ARTICLE_TAG(state, tag) {
      state.articleTag = tag;
    },
    SAVE_NEWS_SEARCH(state, search) {
      state.newsSearch = search;
    },
    SAVE_APPROVE_KEY(state, key) {
      state.approveKey = key;
    },
    SAVE_LOGIN_INFO_LINK_SOURCE(state, source) {
      if (source !== null) state.linkSource = source;
    },
    SAVE_COMMERCIAL_LIST(state, list) {
      state.commercialList = list.map((item: ICommercialOrigin) => ({
        id: item.SGID,
        img: item.filename,
        url: item.linkurl,
      }));
    },
    SAVE_TOP_COMMERCIAL_LIST(state, list) {
      state.topCommercialList = list.map((item: ICommercialOrigin) => ({
        id: item.SGID,
        img: item.filename,
        url: item.linkurl,
      }));
    },
    SAVE_MESSAGE_TOKEN(state, token) {
      state.messageToken = token;
    },
    SAVE_DEVICE_INFO(state, device) {
      state.device = device;
    },
    SAVE_TEMP_LINK_FLAG(state, flag) {
      state.tempLinkFlag = flag;
    },
  },
  actions: {
    saveLoginInfoTemp({ commit }, info: IInputInfo) {
      if (info.Mobile) commit('SAVE_LOGIN_INFO_MOBILE', info.Mobile);
      if (info.SnapCode) commit('SAVE_LOGIN_INFO_SNAPCODE', info.SnapCode);
    },
    saveArticleTag({ commit }, tag: string) {
      commit('SAVE_ARTICLE_TAG', tag);
    },
    saveNewsSearch({ commit }, search: string) {
      commit('SAVE_NEWS_SEARCH', search);
    },
    saveMessageToken({ commit }, token: string) {
      commit('SAVE_MESSAGE_TOKEN', token);
    },
    saveDeviceInfo({ commit }, isIos: boolean) {
      commit('SAVE_DEVICE_INFO', isIos ? 'I' : 'A');
    },
    resetLoginInfoMobile({ commit }) {
      commit('SAVE_LOGIN_INFO_MOBILE', '');
    },
    resetLoginInfoCode({ commit }) {
      commit('SAVE_LOGIN_INFO_SNAPCODE', '');
    },
    changeRegisterModeAct({ commit }, flag: boolean) {
      commit('CHANGE_REGISTER_MODE', flag);
    },
    refreshToken({ commit }) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/GetToken',
            {
              serid: '4010',
              Security: 'A3B0B958-DC13-4827-B663-3E4C130F28D6',
            })
          .then((response) => {
            commit('REFRESH_TOKEN', response.data.token);
            resolve(response.status);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    addMember({ commit }, params) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/AddMember', params)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') {
              if (!params.Status) commit('SAVE_PERSON_ID', data.Data.UserGuid);
              if (!params.Status && (params.Source === 'CF01' || params.Source === 'CF02')) {
                commit('SAVE_APPROVE_KEY', params.Approve_Key);
                commit('SAVE_LOGIN_INFO_EMAIL', params.Email);
                commit('SAVE_LOGIN_INFO_NAME', params.Name);
                commit('SAVE_LOGIN_INFO_SOURCE', params.Source);
              }
              if (params.Status === '1') commit('SAVE_LOGIN_INFO_MOBILE', params.Mobile);
              // if (params.PID) commit('SAVE_LOGIN_INFO_CK_ID', params.PID);
            }
            resolve(data);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    loginAndLoadBaseUser({ dispatch }, params) {
      let loginData = {};
      return new Promise((resolve, reject) => {
        dispatch('loginMember', params)
          .then((data) => {
            loginData = data;
            if (data.Code === '0000') {
              return dispatch('loadBaseUser');
            }
            return resolve;
          })
          .then(() => {
            resolve(loginData);
          })
          .catch(() => {
            reject();
          });
      });
    },
    loginMember({ commit, state }, params) {
      const temp = {
        ...params,
        RegistrDriver: state.device,
        RegistrationID: state.messageToken,
      };
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/LoginMember', temp)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') {
              commit('SAVE_PERSON_ID', data.Data[0].UserGuid);
              commit('SAVE_LOGIN_INFO_MOBILE', data.Data[0].Mobile);
              commit('SAVE_LOGIN_INFO_NAME', data.Data[0].Name);
              commit('SAVE_LOGIN_INFO_EMAIL', data.Data[0].Email);
              commit('SAVE_LOGIN_INFO_SOURCE', data.Data[0].Source);
              commit('SAVE_LOGIN_INFO_CK_ID', data.Data[0].PID);
              commit('SAVE_LOGIN_INFO_LINK_SOURCE', data.Data[0].LinkSource);
              if (params.Approve_Key) commit('SAVE_APPROVE_KEY', params.Approve_Key);
              const cookieTemp = {
                Source: params.Source,
                // eslint-disable-next-line
                Approve_Key: params.Source !== 'CF05' ? params.Approve_Key : undefined,
                Email: params.Source !== 'CF05' ? params.Email : undefined,
                Mobile: params.Source === 'CF05' ? params.Mobile : undefined,
                Pa5wpd: params.Source === 'CF05' ? params.Pa5wpd : undefined,
                RegistrDriver: state.device,
                RegistrationID: state.messageToken,
              };
              localStorage.setItem('ckapp', btoa(JSON.stringify(cookieTemp)));
              // document.cookie = `ckapp=${btoa(JSON.stringify(cookieTemp))}`;
            }
            resolve(response.data);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getMemberUser({ commit }, params) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/GetMemberUser', params)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') {
              if (params.Source === 'CF05') {
                commit('SAVE_LOGIN_INFO_MOBILE', params.Mobile);
                commit('SAVE_LOGIN_INFO_SNAPCODE', params.SnapCode);
                resolve(data);
              } else resolve(data.Data[0].UserGuid);
            } else reject(data.Message);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    loadBaseUser({ commit, rootState }) {
      const temp = {
        UserGuid: rootState.userId,
      };
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/LoadBaseUser', temp)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') {
              commit('SAVE_LOGIN_INFO_ID', data.Data[0].ID);
              commit('SAVE_LOGIN_INFO_ADDRESS', data.Data[0].address);
              commit('SAVE_LOGIN_INFO_MEMBER_ID', data.Data[0].NMemberID);
              commit('SAVE_LOGIN_INFO_NAME', data.Data[0].RealName);
              commit('SAVE_LOGIN_INFO_MOBILE', data.Data[0].mobile);
              commit('SAVE_LOGIN_INFO_EMAIL', data.Data[0].Email);
            }
            resolve(data);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    saveBaseUser({ commit, rootState }, params) {
      const temp = {
        RealName: params.RealName,
        Email: params.Email,
        ID: params.ID,
        mobile: params.Mobile,
        address: params.address,
        UserGuid: rootState.userId,
      };
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/SaveBaseUser', temp)
          .then((response) => {
            const { data } = response;
            resolve(data);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getCommercialList({ commit }, id) {
      const temp = {
        SerIDSite: null,
        LayoutID: id ? 'HBT00001' : 'HBN00004',
        Count: 6,
        Classification: '4',
        ClassKind: null,
        SortFree: true,
      };

      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/Commercial/GetList', temp)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000' && data.Data) {
              if (id) commit('SAVE_TOP_COMMERCIAL_LIST', data.Data);
              else commit('SAVE_COMMERCIAL_LIST', data.Data);
            }
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    addCkMember({ commit }, params) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/AddMember', params)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') {
              resolve(data.Data.UserGuid);
            } else reject(data.Message);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    mergeUserApply({ commit }, params) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/MergeUserApply', params)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') {
              commit('SAVE_TEMP_LINK_FLAG', true);
              resolve();
            } else reject(data.Message);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
  },
  modules: {
    pastTest,
    testKind,
    ticket,
    tags,
    onlineTest,
    subscription,
    article,
    task,
    examInfo,
    site,
    notification,
  },
};

export default new Vuex.Store<RootState>(store);
