<template>
  <div id="app">
<!--    <div id="nav">-->
<!--      <router-link to="/">Home</router-link> |-->
<!--      <router-link to="/about">About</router-link>-->
<!--    </div>-->
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive">
        <!-- Cache View -->
      </router-view>
    </keep-alive>
    <router-view v-if="!$route.meta.keepAlive">
      <!-- No cache view -->
    </router-view>
    <div style="position: absolute; top: 0; width: 100vw; height: 100vh; background: rgba(0, 0, 0, 0.3);
                display: flex; justify-content: center; align-items: center"
         v-if="showInstall && !appInstalled">
      <div style="background: #FFFFFF; width: 245px; padding: 16px; border-radius: 12px;
                      text-align: left; color: rgba(0, 0, 0, 0.8)">
        <div style="font-size: 18px; font-weight: bold; margin-bottom: 6px">
          
        </div>
        <div style="font-size: 14px; margin-bottom: 14px">
          
        </div>
        <div style="display: flex; justify-content: flex-end">
          <div style="color: #2F80ED; font-size: 16px; text-align: center; margin-right: 10px" @click="cancel"></div>
          <div style="color: #2F80ED; font-size: 16px; text-align: center " @click="install"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { mapActions } from 'vuex';

  export default {
    components: {},
    data() {
      return {
        showBlock: false,
        showInstall: false,
        appInstalled: false,
        deferredPrompt: '',
      };
    },
    created() {
      // eslint-disable-next-line
      this.$messaging.getToken({ vapidKey: 'BJkQPCtaI_J4yTd9dZQkWmkTVsWObO3uPQ4dIvjnHDphTWaoJEqq67KVFoFCEabMeivM8o_1fgAAX84iCdiLig0' })
        .then((token) => {
          this.saveMessageToken(token);
          console.log(token);
        });

      this.$messaging.onMessage((payload) => {
        if (Notification.permission === 'granted') {
          navigator.serviceWorker.ready.then((registration) => {
            registration.showNotification(payload.notification.NotifSubject, {
              body: payload.notification.NotifContent,
            });
          });
        }
      });

      window.onappinstalled = (event) => {
        this.appInstalled = true;
      };

      // alert(`isInStandaloneMode: ${this.isInStandaloneMode}, isNotification: ${this.isNotification}`);
      if (this.isInStandaloneMode) {
        Notification.requestPermission((status) => {
          console.log('Notification permission status:', status);
        });
      }

      this.saveDeviceInfo(this.isIos);

      window.addEventListener('beforeinstallprompt', (e) => {
        if (this.isIos) return;
        // Prevent the mini-infobar from appearing on mobile
        e.preventDefault();
        // Stash the event so it can be triggered later.
        this.deferredPrompt = e;
        // Update UI notify the user they can install the PWA
        this.showInstall = true;
        // this.showInstallHandler();
      });
    },
    // mounted() {
    //   setTimeout(() => {
    //     if (this.isIos && !this.isInStandaloneMode) {
    //       alert(' ');
    //     }
    //   }, 500);
    // },
    computed: {
      isIos: () => {
        const userAgent = window.navigator.userAgent.toLowerCase();
        return /iphone|ipad|ipod/.test(userAgent);
      },
      isInStandaloneMode: () => (('standalone' in window.navigator) && (window.navigator.standalone))
        || (window.matchMedia('(display-mode: standalone)').matches),
      isNotification: () => ('Notification' in window),
    },
    methods: {
      ...mapActions([
        'saveMessageToken',
        'saveDeviceInfo',
      ]),
      install() {
        this.deferredPrompt.prompt();
      },
      cancel() {
        this.showInstall = false;
      },
    },
  };
</script>

<style>
  @import "assets/app.css";
  @import "assets/exam.css";
</style>
